import * as React from "react";
import type { HeadFC } from "gatsby";
import { useAuth0 } from "@auth0/auth0-react";
import { Link, navigate } from "gatsby";
import classNames from "classnames";

import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import styled from "styled-components";
import Dropdown from "react-bootstrap/Dropdown";
import Button from "react-bootstrap/Button";

const ICON = styled.div`
  height: 32px;
  width: 32px;
  background-color: #f5f5f5;
  margin-right: 14px;
`;

const CustomToggle = React.forwardRef(({ children, onClick }, ref) => (
  <a
    href=""
    ref={ref}
    onClick={(e) => {
      e.preventDefault();
      onClick(e);
    }}
  >
    {children}
    &#x25bc;
  </a>
));

const IndexPage = ({ children }) => {
  const {
    user,
    isAuthenticated,
    isLoading,
    loginWithRedirect,
    logout,
    getAccessTokenSilently,
  } = useAuth0();

  return (
    <main>
      <div className="navbar navbar-expand-lg navbar-light bg-brand-1 d-flex">
        <Container>
          <div className="d-flex flex-row">
            <div className="d-flex justify-content-center  align-items-center">
              <h3>
                <Link to="/">Jarvis</Link>
              </h3>
            </div>
          </div>
          <div>
            {"" && (
              <input
                type="text"
                className="form-control bg-brand-5 color-brand-6"
                id="exampleFormControlInput1"
                placeholder="Search In Web3"
              ></input>
            )}
          </div>

          <div className="text-uppercase d-flex flex-direction-row gap-6 color-white gap-3">
            {}

            {isAuthenticated ? (
              <Dropdown>
                <Dropdown.Toggle
                  className="color-brand1"
                  as={CustomToggle}
                  variant="success"
                  id="dropdown-basic"
                >
                  {user.name}
                </Dropdown.Toggle>

                <Dropdown.Menu>
                  <Dropdown.Item as="div">
                    {" "}
                    <Link to="/">Start</Link>
                  </Dropdown.Item>
                  <Dropdown.Item as="div" onClick={() => logout()}>
                    Logout
                  </Dropdown.Item>
                </Dropdown.Menu>
              </Dropdown>
            ) : (
              <div
                className="defaultCursor"
                onClick={() => loginWithRedirect()}
              >
                Login
              </div>
            )}
          </div>
        </Container>
      </div>
      <Container fluid>
        <Row>
          <Col xs={12} extraClass="col-page">
            {children}
          </Col>
        </Row>
      </Container>
    </main>
  );
};

export default IndexPage;

export const Head: HeadFC = () => <title>Home Page</title>;
